.home_page {
    min-height: 800px;
    max-height: 1000px;
    height: 100vh;
    width:100vw;
    background-image: url(../images/pages/background_home.webp);
    background-size: cover;
    display: flex;
    flex-direction: column; /* Aligne les éléments en colonne */
